:root{
    --white-shadow: 2px 2px 0px white;
}

.homeSection{
    position: relative;
}
    .miNombre{
        font-size: 40px;
        color: var(--neon-green);
        text-shadow: var(--white-shadow);
        width: fit-content;
        position: absolute;
        top: 0px;
        left: 50px;
    }
    .subtitle{
        font-size: 18px;
        width: fit-content;
        transform: translateX(-50%);
        position: absolute;
        top: 50px;
        left: 378px;
    }
    .sobremi{
        font-size: 30px;
        position: absolute;
        right: 30px;
        top: 80px;
        color: var(--neon-violet);
        text-align: right;

        .content{
            font-size: 17px;
            color: var(--text-color);
            max-width: 700px;
            line-height: 25px;
        }
    }